.Blur {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25); }

.RulesButton {
  position: absolute;
  right: 3vw;
  bottom: 3vh; }

.RulesButton, .Tumbler button {
  width: 25vh;
  height: 8vh;

  background: transparent;
  border: rgba(255, 255, 255, 0.5) 2px solid;
  border-radius: 3vh;

  color: white;
  font-weight: 700;
  letter-spacing: .2vw;
  font-size: 2rem;

  cursor: pointer;
  transition: .2s;
  &:hover {
    transform: scale(1.1); } }

.Tumbler {
  position: absolute;
  left: 3vw;
  bottom: 3vh; }

.RulesWindow {
  z-index: 2;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;

  top: 50%;
  transform: translate(0,-50%);

  width: 30vw;
  height: 30vw;

  background: white;
  border-radius: 2vw;

  img.Rule {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 2vw;
    width: 22vw; }

  button {
    border: none;
    background: transparent;
    position: absolute;
    margin-top: 2.7vw;
    right: 3vw;
    cursor: pointer;
    transition: .2s;
    &:hover {
      transform: scale(1.2); } }

  p {
    position: absolute;
    margin-top: 2vw;
    margin-left: 2vw;
    color: #565469;
    font-size: 3vw;
    font-weight: 800; }
  img {
    width: 2vw; } }

@media screen and (orientation: portrait) {
  .RulesButton, .Tumbler button {
    font-size: 4vh; }

  .RulesWindow {
    width: 100%;
    height: 100%;
    border-radius: 0;
    p {
      left: 0;
      right: 0;
      width: 13vh;
      font-size: 5vh;
      margin: 10vh auto 0; }
    img.Rule {
      top: 50%;
      transform: translate(0,-50%);
      width: 40vh; }
    button {
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0 auto 10vw;
      img {
        width: 5vh; } } } }

