div.Triangle_locate {
  div {
    left: 0;
    right: 0;
    margin: 0 auto; }
  div.rock {
    position: absolute;
    top: 11vw; }
  div.scissors {
    position: absolute;
    right: -17vw;
    top: -4vw; }
  div.paper {
    position: absolute;
    left: -17vw;
    top: -4vw; } }

div.Pentagon_locate {
  margin-top: 3vw;
  div {
    left: 0;
    right: 0;
    margin: 0 auto;
    scale: 0.85; }

  div.rock {
    position: absolute;
    top: 12vw;
    right: -13vw; }

  div.scissors {
    position: absolute;
    right: 0;
    left: 0;
    margin: -10vw auto; }

  div.paper {
    position: absolute;
    right: -19vw;
    margin-top: -3vw; }

  div.spock {
    position: absolute;
    left: -19vw;
    margin-top: -3vw; }

  div.lizard {
    position: absolute;
    top: 12vw;
    left: -13vw; } }

@media screen and (orientation: portrait) {
  div.Triangle_locate {
    div {
      scale: 1.2; }
    div.rock {
      top: 15vh; }

    div.scissors {
      right: -50vw;
      margin-top: -10vh; }

    div.paper {
      left: -50vw;
      margin-top: -10vh; } }

  div.Pentagon_locate {
    div {
      scale: 1.1; }
    div.rock {
      top: 17vh;
      right: 40vw; }

    div.scissors {
      margin: -20vh auto; }

    div.paper {
      position: absolute;
      right: 60vw;
      top: -3vh; }

    div.spock {
      position: absolute;
      left: 60vw;
      top: -3vh; }

    div.lizard {
      position: absolute;
      top: 17vh;
      left: 40vw; } } }
